import React from 'react';
import MovieCard from '../../components/MovieCard';
import { useGenres } from '../../hooks/useGenres';
import {
  GenresContainer,
  GenresHeader,
  GenresTitle,
  GenresSubtitle,
  GenresGrid,
  GenreCard,
  GenreName,
  GenreCount,
  MoviesSection,
  MoviesHeader,
  MoviesTitle,
  MoviesCount,
  MoviesGrid,
  LoadingSpinner,
  ErrorMessage,
  EmptyState,
  EmptyIcon,
  EmptyTitle,
  EmptyDescription,
  LoadMoreButton,
  LoadMoreContainer,
  SearchResultsHeader
} from './styles';

const GenresPage: React.FC = () => {
  const {
    genres,
    selectedGenre,
    movies,
    loading,
    error,
    hasMore,
    searchQuery,
    handleGenreSelect,
    loadMore
  } = useGenres();

  return (
    <GenresContainer>
      <GenresHeader>
        <GenresTitle> </GenresTitle>
        <GenresSubtitle>
              
        </GenresSubtitle>
      </GenresHeader>

      {!searchQuery && (
        <GenresGrid>
          {genres.map((genre) => (
            <GenreCard
              key={genre.id}
              isSelected={selectedGenre?.id === genre.id}
              onClick={() => handleGenreSelect(genre)}
            >
              <GenreName>{genre.name}</GenreName>
              <GenreCount> </GenreCount>
            </GenreCard>
          ))}
        </GenresGrid>
      )}

      {(selectedGenre || searchQuery) && (
        <MoviesSection>
          <MoviesHeader>
            {searchQuery ? (
              <SearchResultsHeader>
                <MoviesTitle>"{searchQuery}"  </MoviesTitle>
                <MoviesCount>{movies.length} </MoviesCount>
              </SearchResultsHeader>
            ) : (
              <>
                <MoviesTitle>{selectedGenre?.name} </MoviesTitle>
                <MoviesCount>{movies.length} </MoviesCount>
              </>
            )}
          </MoviesHeader>

          {error ? (
            <ErrorMessage>
              <h3> </h3>
              <p>{error}</p>
            </ErrorMessage>
          ) : movies.length > 0 ? (
            <>
              <MoviesGrid>
                {movies.map((movie) => (
                  <MovieCard key={movie.id} movie={movie} />
                ))}
              </MoviesGrid>
              
              {hasMore && (
                <LoadMoreContainer>
                  <LoadMoreButton onClick={loadMore} disabled={loading}>
                    {loading ? ' ...' : ''}
                  </LoadMoreButton>
                </LoadMoreContainer>
              )}
            </>
          ) : (
            <EmptyState>
              <EmptyIcon></EmptyIcon>
              <EmptyTitle>
                {searchQuery ? '  ' : ' '}
              </EmptyTitle>
              <EmptyDescription>
                {searchQuery 
                  ? `"${searchQuery}"      .`
                  : '     .'
                }
              </EmptyDescription>
            </EmptyState>
          )}
        </MoviesSection>
      )}

      {!selectedGenre && !searchQuery && (
        <EmptyState>
          <EmptyTitle> </EmptyTitle>
          <EmptyDescription>
                  .
          </EmptyDescription>
        </EmptyState>
      )}
    </GenresContainer>
  );
};

export default GenresPage; 