import React from "react";
import MovieCard from "../../components/MovieCard";
import { useFavorites } from "../../hooks/useFavorites";
import { Section, SectionTitle, Grid } from "../../components/styled/CommonStyles";
import {
  FavoritesContainer,
  Header,
  Title,
  Subtitle,
  StatsContainer,
  StatCard,
  StatNumber,
  StatLabel,
  ActionsContainer,
  ClearButton,
  EmptyState,
  EmptyIcon,
  EmptyTitle,
  EmptyDescription,
  BrowseButton,
  FavoritesSection
} from "./styles";

const FavoritesPage: React.FC = () => {
  const {
    favorites,
    recentViewed,
    handleFavoriteToggle,
    handleClearFavorites,
    handleClearRecent,
    handleBrowseMovies
  } = useFavorites();

  return (
    <FavoritesContainer>
      <Header>
        <Title>  </Title>
        <Subtitle>     </Subtitle>
      </Header>

      <StatsContainer>
        <StatCard>
          <StatNumber>{favorites.length}</StatNumber>
          <StatLabel> </StatLabel>
        </StatCard>
        <StatCard>
          <StatNumber>{recentViewed.length}</StatNumber>
          <StatLabel>  </StatLabel>
        </StatCard>
      </StatsContainer>

      <ActionsContainer>
        {favorites.length > 0 && (
          <ClearButton onClick={handleClearFavorites}>
               
          </ClearButton>
        )}
        {recentViewed.length > 0 && (
          <ClearButton onClick={handleClearRecent}>
               
          </ClearButton>
        )}
      </ActionsContainer>

      {favorites.length === 0 && recentViewed.length === 0 ? (
        <EmptyState>
          <EmptyIcon></EmptyIcon>
          <EmptyTitle>   </EmptyTitle>
          <EmptyDescription>
                   .
          </EmptyDescription>
          <BrowseButton onClick={handleBrowseMovies}>
             
          </BrowseButton>
        </EmptyState>
      ) : (
        <>
          {favorites.length > 0 && (
            <FavoritesSection>
              <SectionTitle>  ({favorites.length})</SectionTitle>
              <Grid columns={4}>
                {favorites.map((movie) => (
                  <MovieCard
                    key={movie.id}
                    movie={movie}
                    onFavoriteToggle={handleFavoriteToggle}
                  />
                ))}
              </Grid>
            </FavoritesSection>
          )}

          {recentViewed.length > 0 && (
            <FavoritesSection>
              <SectionTitle>   ({recentViewed.length})</SectionTitle>
              <Grid columns={4}>
                {recentViewed.map((movie) => (
                  <MovieCard
                    key={movie.id}
                    movie={movie}
                    onFavoriteToggle={handleFavoriteToggle}
                  />
                ))}
              </Grid>
            </FavoritesSection>
          )}
        </>
      )}
    </FavoritesContainer>
  );
};

export default FavoritesPage;