import React from 'react';
import { useSettings } from '../../hooks/useSettings';
import {
  SettingsContainer,
  SettingsSection,
  SettingItem,
  SettingInfo,
  SettingTitle,
  SettingDescription,
  ToggleSwitch,
  ToggleInput,
  ToggleSlider,
  DangerZone,
  DangerTitle,
  DangerDescription,
  DangerButton
} from './styles';
import { SectionTitle } from '../../components/styled/CommonStyles';

interface SettingsPageProps {
  isDarkMode: boolean;
  setIsDarkMode: (isDark: boolean) => void;
}

const SettingsPage: React.FC<SettingsPageProps> = ({ isDarkMode, setIsDarkMode }) => {
  const { handleClearFavorites, handleClearRecent, handleClearAllData } = useSettings();

  return (
    <SettingsContainer>
      <SectionTitle></SectionTitle>
      
      <SettingsSection>
        <SettingItem>
          <SettingInfo>
            <SettingTitle> </SettingTitle>
            <SettingDescription>
                   .
            </SettingDescription>
          </SettingInfo>
          <ToggleSwitch>
            <ToggleInput
              type="checkbox"
              checked={isDarkMode}
              onChange={(e) => setIsDarkMode(e.target.checked)}
            />
            <ToggleSlider />
          </ToggleSwitch>
        </SettingItem>
      </SettingsSection>

      <DangerZone>
        <DangerTitle> </DangerTitle>
        <DangerDescription>
               .     .
        </DangerDescription>
        
        <div style={{ display: 'flex', gap: '12px', flexWrap: 'wrap' }}>
          <DangerButton onClick={handleClearFavorites}>
              
          </DangerButton>
          <DangerButton onClick={handleClearRecent}>
               
          </DangerButton>
          <DangerButton onClick={handleClearAllData}>
              
          </DangerButton>
        </div>
      </DangerZone>
    </SettingsContainer>
  );
};

export default SettingsPage; 