import React from 'react';
import { useNavigate } from 'react-router-dom';
import MovieCard from '../../components/MovieCard';
import SearchBar from '../../components/SearchBar';
import { useMovies } from '../../hooks/useMovies';
import {
  HomeContainer,
  HeroSection,
  HeroTitle,
  HeroSubtitle,
  SearchContainer,
  MovieSection,
  SectionHeader,
  EmptyState,
  LoadingSpinner,
  ErrorMessage,
  Grid,
  SectionTitle,
  LoadMoreButton,
  LoadMoreContainer
} from './styles';

const HomePage: React.FC = () => {
  const navigate = useNavigate();
  const { 
    popularMovies, 
    trendingMovies, 
    recentBasedMovies, 
    loading, 
    error,
    hasMorePopular,
    hasMoreTrending,
    hasMoreRecentBased,
    loadMorePopular,
    loadMoreTrending,
    loadMoreRecentBased
  } = useMovies();

  const handleSearch = (query: string) => {
    navigate(`/genres?search=${encodeURIComponent(query)}`);
  };

  if (loading) {
    return (
      <HomeContainer>
        <LoadingSpinner />
      </HomeContainer>
    );
  }

  if (error) {
    return (
      <HomeContainer>
        <ErrorMessage>
          <h3> </h3>
          <p>{error}</p>
        </ErrorMessage>
      </HomeContainer>
    );
  }

  return (
    <HomeContainer>
      <HeroSection>
        <HeroTitle>MoviePick</HeroTitle>
        <HeroSubtitle>
           .
        </HeroSubtitle>
        <SearchContainer>
          <SearchBar onSearch={handleSearch} placeholder="  ." />
        </SearchContainer>
      </HeroSection>

      <MovieSection>
        <SectionHeader>
          <SectionTitle> </SectionTitle>
        </SectionHeader>
        <Grid columns={4}>
          {popularMovies.map((movie) => (
            <MovieCard key={movie.id} movie={movie} />
          ))}
        </Grid>
        {hasMorePopular && (
          <LoadMoreContainer>
            <LoadMoreButton onClick={loadMorePopular}>
              
            </LoadMoreButton>
          </LoadMoreContainer>
        )}
      </MovieSection>

      <MovieSection>
        <SectionHeader>
          <SectionTitle> </SectionTitle>
        </SectionHeader>
        <Grid columns={4}>
          {trendingMovies.map((movie) => (
            <MovieCard key={movie.id} movie={movie} />
          ))}
        </Grid>
        {hasMoreTrending && (
          <LoadMoreContainer>
            <LoadMoreButton onClick={loadMoreTrending}>
              
            </LoadMoreButton>
          </LoadMoreContainer>
        )}
      </MovieSection>

      {recentBasedMovies.length > 0 && (
        <MovieSection>
          <SectionHeader>
            <SectionTitle>    </SectionTitle>
          </SectionHeader>
          <Grid columns={4}>
            {recentBasedMovies.map((movie) => (
              <MovieCard key={movie.id} movie={movie} />
            ))}
          </Grid>
          {hasMoreRecentBased && (
            <LoadMoreContainer>
              <LoadMoreButton onClick={loadMoreRecentBased}>
                
              </LoadMoreButton>
            </LoadMoreContainer>
          )}
        </MovieSection>
      )}

      {recentBasedMovies.length === 0 && (
        <MovieSection>
          <SectionTitle>    </SectionTitle>
          <EmptyState>
            <h3>   </h3>
            <p>       .</p>
          </EmptyState>
        </MovieSection>
      )}
    </HomeContainer>
  );
};

export default HomePage; 